<template>
    <div class="my-popups">
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered" style="max-width:900px !important">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">
                            {{ $t('Attachments') }}
                        </h5>
                        <button v-on:click="ClosePopup()"  type="button" class="close" data-original-title="Close"><em
                                class="fa fa-times"></em></button>

                    </div>
                    <div class="modal-body">
                        <Form ref="tagForm" autocomplete="off">
                            <div class="col-md-12">
                                <div class="form-group">
                                    <div class="listing OuterMainDiv">
                                        <div class="row" style="max-height:500px;overflow-y:auto;">
                                            <dynamic-form lang="en" v-if="DataLoaded" :buttons="buttons" :schema="FormSchema"></dynamic-form>
                                        </div>
                                    </div>

                                </div>
                            </div>
                        </Form>
                    </div>

                    <div class="listing tblassestgroupinfodetails px-1 mt-3">
                        <div class="table-responsive ">
                            <table id="tblassestgroupinfodetails" class="table table-bordered dt-responsive nowrap mt-0">
                                <thead class="thead-bg">

                                    <tr>
                                       <th>Serial Number</th>
                                        <th>Attachment Name</th>
                                        <th>File Name</th>
                                        <th>Used For Terms And Conditions</th>
                                        <th>Terms and Conditions Start Date</th>
                                        <th>Action</th>
                                    </tr>

                                </thead>
                              
                                <tbody>
                                    <tr v-for="(item, index) in arryaofData" :key="index"> 
                                        <td class="text-overflow-dynamic-container">
                                            {{item.SERIAL_NUMBER}}
                                        </td>
                                        <td>
                                            <span class="text-overflow-dynamic-container">{{ item.ATTACHMENT_NAME }}</span>
                                        </td>
                                        <td>
                                            <span class="text-overflow-dynamic-container">{{ item.ATTACHMENT_PATH }}</span>
                                        </td>
                                        <td>
                                            <span class="text-overflow-dynamic-container">{{ item.IS_FOR_TERMS_CONDITION }}</span>
                                        </td>
                                        <td>
                                            <span class="text-overflow-dynamic-container">{{ item.TERMS_CONDITION_STARTS_AT }}</span>
                                        </td>
                                        
                                        <td class="text-center single-action">
                                            <a href="javascript:;" @click="Downloadfiles(item)" class="downloadfile" title="" value="2332" filename="checkfalg.png" filepath="files/78376/2675/d4f2c8f6-0b4a-45e7-ba61-255fd4f87775.png" filetype="image/png" data-toggle-tooltip="tooltip" data-original-title="Download">
                                                <i class="fa fa-download text-primary action-icon" title="Download Attachment"></i>
                                            </a>
                                                <a href="javascript:;" class="deletefile" title="" value="2332" filepath="files/78376/2675/d4f2c8f6-0b4a-45e7-ba61-255fd4f87775.png" filetype="image/png" data-toggle-tooltip="tooltip" data-original-title="Delete">
                                                    <i class="fa fa-trash text-danger action-icon" title="Remove Attachment"></i>
                                                </a>
                                        </td>
                                    </tr>
                                </tbody>
                                </table>
                        </div>
                    </div>

                    
            </div>
        </div>
    </div>
</div>
</template>
<script>
import DataService from '../../service/DataService';

export default {
    props:{
        AssetDetails:{
            type:String
     }
     },
    data(){
        return{ 
            
            arryaofData:[],
            buttons: [
                

                {
                    type: "",
                    onClick: this.ClosePopup
                }
            ]
        }
    },
    created:function(){
     var vm = this;
     vm.GetViewFiles();
    },
    methods:{
        ClosePopup()
        {
            var vm=this;
            vm.$parent.CloseViewFilePopup();
        },
        GetViewFiles() {
            var vm = this;
            var obj= `catalogueId=${vm.AssetDetails.ASSET_CATALOGUE_ID}&isDelete=1&flag=`
            DataService.AttachViewFiles(obj).then(response => {
            vm.arryaofData= JSON.parse(response.data).data;

            })
        },

        Downloadfiles(item){
            var vm=this;
            var param=`filePath=${item.ATTACHMENT_PATH}`;
            DataService.DownloadAttachment(param).then(function (response) {
                const byteData=response.data;
                const fileURL = `data:${item.ATTACHMENT_TYPE};base64,${byteData}`;
                var fileLink = document.createElement('a');
                fileLink.href = fileURL;
                fileLink.setAttribute('download', item.ATTACHMENT_NAME);
                document.body.appendChild(fileLink);
                fileLink.click();
            });  
        }
    }
    
}
</script>
